.filters {
    width: 100%;
    height: 65px;
    display: flex;
    justify-content: space-around;
    border-radius: 20px;
    padding: 1px 18px 8px 18px;
    background: #ffffff;
}

.filters .select {
    width: 350px !important;
}

@media screen and (max-width: 768px) {
    .filters {
        display: none;
    }
}

